import React from 'react'
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';

import './LogingPage.css'

export default function LoginPage() {
    return (
        <>
            <Box
                component="form" sx={{
                    '& .MuiTextField-root': { m: 1, width: '25ch' },
                }}
                noValidate
                autoComplete="off"
            >
                <div className="login_page_component">
                    <TextField
                        id="outline-name-input"
                        label="Usuario"
                        type="text"
                    />
                    <TextField
                        id="outline-password-input"
                        label="password"
                        type="password"
                    />
                   
                   <a className="ef-btn" href="*" role="submit">Entrar <span></span></a>

                    
                </div>
            </Box>
        </>
    )
}